<template>
  <base-layout>
    <h2 class="text-center my-30px">
      打工总收益:
      <strong>
        {{ total }} gold（约等于 {{ (total * goldPrice).toFixed(2) }} usd）
      </strong>
    </h2>
    <a-row :gutter="[16, 16]">
      <a-col :md="12" :sm="24">
        <a-alert type="info" message="钱包地址" class="h-284px wallet-card">
          <template #description>
            <template v-if="account">
              <span
                  style="cursor: pointer; margin-right: 5px"
                  id="account"
                  :data-clipboard-text="account"
              >
                {{ account }}
              </span>
              <span v-if="copyStatus" style="color: #19be6b">
                <a-icon type="check-circle"/>
                复制成功
              </span>
              <a-tooltip v-else content="点击复制" placement="right">
                <a-icon
                    id="copy-btn"
                    type="copy"
                    :data-clipboard-text="account"
                    size="20"
                    style="color: #1890ff; cursor: pointer"
                />
              </a-tooltip>
            </template>
            <span v-else>暂无地址信息</span>
          </template>
        </a-alert>
      </a-col>
      <a-col :md="12" :sm="24">
        <a-alert type="warning" message="收菜说明">
          <template #description>
            <div style="word-break: break-all">
              <p>1. 收菜服务直接与官方合约交互，无安全风险，认准官方收菜合约</p>
              <p style="text-indent: 15px">
                兼职合约地址：
                <a
                    target="_blank"
                    href="https://bscscan.com/address/0xe278BDF4541cc309b379F9A4E867F60fD6B7BC59"
                >
                  0xe278BDF4541cc309b379F9A4E867F60fD6B7BC59(可点击链接)
                </a>
              </p>
              <p style="text-indent: 15px">
                全职合约地址：
                <a
                    target="_blank"
                    href="https://bscscan.com/address/0x698E165F2897e4daC68671c4cDFf337bbC543767"
                >
                  0x698E165F2897e4daC68671c4cDFf337bbC543767(可点击链接)
                </a>
              </p>
            </div>
            <p>
              2. 本服务每十张卡需要支付 0.001 bnb (0.5u
              左右)，不足十张按十张计算
            </p>
            <p>
              3. 选中卡进行收菜，
              <strong>
                收菜时会先向本服务转服务费，成功之后会批量弹出收菜交易
              </strong>
            </p>
          </template>
        </a-alert>
      </a-col>
    </a-row>
    <br/>
    <a-row>
      <a-col>
        <PropsTable
            :data-source="propsDataSource"
            :loading="loading"
            v-model="selectedTokens"
            @fire="getAward"
        />
      </a-col>
    </a-row>
  </base-layout>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'

import mixins from "../mixins";
import PropsTable from "../components/harvest/propsTable.vue";
import ClipboardJs from "clipboard";

const workType = {
  fullTime: 1,
  partTime: 2,
};

const careerMap = {
  "0xC6dB06fF6e97a6Dc4304f7615CdD392a9cF13F44": "法师",
  "0x22F3E436dF132791140571FC985Eb17Ab1846494": "战士",
  "0xF31913a9C8EFE7cE7F08A1c08757C166b572a937": "游侠",
  "0xaF9A274c9668d68322B0dcD9043D79Cd1eBd41b3": "盗贼",
  "0x819E04ddE93600b224F65e3C9B51b1B4D9fBa3b5": "卡特琳娜",
};

export default {
  name: "Harvest",
  mixins: [mixins],
  components: {
    PropsTable,
  },
  data() {
    return {
      total: 0,
      tokens: [],
      selectedTokens: [],
      loading: true,
      feeRate: 10,
      baseFee: 0.001,
      propsDataSource: [],
      copyStatus: 0,
      goldPrice: 0,
    };
  },
  async created() {
    await this.initWeb3Account();
    if (!this.account) {
      return;
    }

    this.initContract();
    this.getGoldInfo();
    this.goldPrice = await this.updateGoldPrice()
    console.log(this.goldPrice)
  },
  mounted() {
    window.getProperty = this.getProperty;
    const accountBtn = new ClipboardJs("#account");
    const copyBtn = new ClipboardJs("#copy-btn");

    const successCb = () => {
      this.copyStatus = true;
      setTimeout(() => {
        this.copyStatus = false;
      }, 3000);
    };

    copyBtn.on("success", successCb);
    accountBtn.on("success", successCb);
  },
  methods: {
    async getGoldInfo() {
      const gold = this.web3.utils.toBN(0);
      const partTimeTokenCount = await this.partTimeWork.methods
          .balanceOf(this.account)
          .call();
      const fullWorkTokenCount = await this.work.methods
          .balanceOf(this.account)
          .call();

      const propsDataSource = [];

      for (let i = 0; i < partTimeTokenCount; i++) {
        const tokenId = await this.partTimeWork.methods
            .tokenOfOwnerByIndex(this.account, i)
            .call();

        let igold = await this.partTimeWork.methods
            .getAwardInfo(tokenId)
            .call({from: this.account});
        igold = this.web3.utils.toBN(igold);
        gold.iadd(igold);
        this.tokens.push({
          token_id: tokenId,
          gold: this.web3.utils.fromWei(igold),
        });

        const tokenProperty = await this.getProperty(tokenId);
        tokenProperty.gold = this.web3.utils.fromWei(igold);
        tokenProperty.workType = workType.partTime;

        propsDataSource.push(tokenProperty);
      }

      for (let i = 0; i < fullWorkTokenCount; i++) {
        const tokenId = await this.work.methods
            .tokenOfOwnerByIndex(this.account, i)
            .call();

        let igold = await this.work.methods
            .getAwardInfo(tokenId)
            .call({from: this.account});
        igold = this.web3.utils.toBN(igold);
        gold.iadd(igold);
        this.tokens.push({
          token_id: tokenId,
          gold: this.web3.utils.fromWei(igold),
        });

        const tokenProperty = await this.getProperty(tokenId);
        tokenProperty.gold = this.web3.utils.fromWei(igold);
        tokenProperty.workType = workType.fullTime;

        propsDataSource.push(tokenProperty);
      }

      this.propsDataSource = propsDataSource;
      this.loading = false;

      this.total = this.web3.utils.fromWei(gold.toString(), "ether");
    },
    async getGoldInfoByTokenId(tokenId, account) {
      // 获取单个 tokenId 的收益, tokenId 必须属于 account，否则无法获取到收益
      // account  如果是获取自己的账号的收益，使用 this.account， 获取其他钱包的收益需要传入参数
      let gold;
      try {
        gold = await this.partTimeWork.methods
            .getAwardInfo(tokenId)
            .call({from: account});
      } catch (e) {
        try {
          gold = await this.work.methods
              .getAwardInfo(tokenId)
              .call({from: account});
        } catch (e) {
          // 不能获取到收益
        }
      }
      return gold;
    },
    calculateFee(tokens) {
      return `${Math.ceil(tokens.length / this.feeRate) * this.baseFee}`;
    },
    async getProperty(tokenId) {
      const data = await this.playContract.methods
          .getPlayerInfoBySet(tokenId)
          .call();

      return {
        tokenId: tokenId,
        // 职业地址
        career_addr: data[1],
        // 职业
        career: careerMap[data[1]],
        // 力量
        strength: data[0][0],
        // 敏捷
        agility: data[0][1],
        // 体质
        physique: data[0][2],
        // 意志
        volition: data[0][3],
        // 智力
        brains: data[0][4],
        // 精神
        charm: data[0][5],
      };
    },
    async getAward() {
      const selectedTokens = this.selectedTokens;
      if (!selectedTokens.length) {
        this.$message.error("至少选择一项后操作");
        return;
      }

      // 支付费用
      this.payFee(
          selectedTokens, // 收菜
          (error) => {
            if (error != undefined) {
              console.log("error: ", error);
              return;
            }
            for (const token of selectedTokens) {
              // 兼职
              if (token.workType === workType.partTime) {
                this.partTimeWork.methods
                    .getAward(token.tokenId)
                    .send({from: this.account});
              } else if (token.workType === workType.fullTime) {
                // 全职
                this.work.methods
                    .getAward(token.tokenId)
                    .send({from: this.account});
              }
            }
          }
      ).catch(() => {
        this.$message.error("支付失败");
      });
    },
    payFee(tokens, callback) {
      const fee = this.calculateFee(tokens);

      return this.web3.eth.sendTransaction(
          {
            from: this.account,
            to: "0x4d09c2578690c7D4285D616f3eB1F53128148738",
            value: this.web3.utils.toWei(fee),
          },
          callback
      );
    },
  },
};
</script>

<style scoped>
@media (max-width: 768px) {
  .wallet-card {
    height: auto;
  }
}
</style>
